import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";

const renderField = ({
  input,
  label,
  type,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class DataValidasi extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-4">
            <div className="form-group">
              <label htmlFor="">Jenis Transaksi</label>
              <Field
                name="jenisTransaksi"
                component="select"
                className="form-control"
              >
                <option value="PEMBAYARAN" key="PEMBAYARAN">
                  PEMBAYARAN
                </option>
                <option value="VALIDASI" key="VALIDASI">
                  VALIDASI
                </option>
              </Field>
            </div>
          </div>
          <div className="col-lg-4">
            <Field
              name="dari"
              label="Dari"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-4">
            <Field
              name="sampai"
              label="Sampai"
              component={renderField}
              type="text"
            />
          </div>
          <div className="col-lg-12">
            <div className="tetx-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}
DataValidasi = reduxForm({
  form: "DataValidasi",
  enableReinitialize: true,
})(DataValidasi);
export default connect()(DataValidasi);
